@import "variables.scss";
/*------------  Layout   -----------*/
.container {
  margin: 0 2rem;
  @media (min-width: 1020px) {
    max-width: 1000px;
    margin: auto;
  }
}

section {
  padding-bottom: 2rem;
  border-bottom: solid 1px $comp-clr;
  margin-bottom: 2rem;
  &:last-child {
    border: none;
    padding-bottom: 0;
    margin-bottom: 0;
  }
}

main {
  background-color: var(--white);
  padding: 1rem;
  margin: 0 1rem 1rem;
  @media (min-width: 1020px) {
    max-width: 1064px;
    margin: 0 auto 1rem;
    padding: 1rem;
  }
}

.fluid {
  @media (min-width: 1020px) {
    margin-right: calc(-50vw + 515px);
    margin-left: calc(-50vw + 515px);
    padding: 1rem;
  }
}
